import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { CompaignComponent } from './Compaign/Compaign.component';
import { AddEditCompaignComponent } from './add-edit-Compaign/add-edit-Compaign.component';
import { ViewComponent } from './view/view.component';

const routes: Routes = [
  {
    path: "",
    data: {
      title: "campaign"
    },
    children: [
      {
        path: "campaign",
        component: CompaignComponent,
        data: {
          title: ""
        }
      },
      {
        path: "add-campaign",
        component: AddEditCompaignComponent,
        data: {
          title: ""
        }
      },
      {
        path: "view-campaign/:id",
        component: ViewComponent,
        data: {
          title: ""
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CompaignRoutingModule { }
