import { Component, OnInit } from '@angular/core';
import { PopupService } from "../../../services/popup/popup.service";
import {Router} from '@angular/router';
import { CampaignService } from 'src/app/idealz/services/campaign.service';

@Component({
  selector: 'app-Compaign',
  templateUrl: './Compaign.component.html',
  styleUrls: ['./Compaign.component.scss']
})
export class CompaignComponent implements OnInit {
    inventoryList : any =[] ;
  constructor(private dialogService: PopupService, 
    private router: Router,
    private campaignService:CampaignService) {
      this.campaignService.campaignListing().subscribe(res=>{
        this.inventoryList=res.data;
      })
     }

  ngOnInit() {
  }

  addCompaign(){
    this.router.navigate(['/campaign/add-campaign'])
    // this.dialogService.addCompaign().subscribe(res => {
    //   if(res){
    //   this.inventoryList.push(res);}
    // });
  }

   onViewCampaign(item) {
    this.router.navigate(['/campaign/view-campaign',item.id]);
  }
  onDeleteCampaign(id:any){
this.campaignService.deleteCampaign(id).subscribe(res=>{
  window.location.reload();
})
  }
}
